*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container{
  width: 100%;
  padding: 1em;
  text-align: center;
}
.flex{
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  min-height: 66vh;
  align-items: center;

}
.box{
  padding: 0.5em;
  border-radius: 8px;
  width: 100%;
  margin: 1em 0;
  font-size: 1.5em;
}
.box.confirmed{
  border:1px solid #ce9b61;
  background: #f0bc81;

}
.box.recovered{
  border:1px solid #95c458;
  background: #c0f081;

}
.box.deaths{
  border:1px solid #ce8c61;
  background: #f08e81;

}

.dropdown{
  padding: 0.5em;
  margin: 1em 0;
  font-size: 1.2em;
}
.dropdown option{
  word-wrap: break-word;
}
.covidTable{
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
  display: flex;
}
.covidData{
  font-size: 1.2em;
}
.covidData td,.covidData th{
  border:1px solid #ddd;
  padding: 8px;

}

.covidData tr:nth-child(even){background-color: #f2f2f2;}
.covidData tr:hover {background-color: #ddd;}
.covidData th{
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  background-color: #4CAF50;
  color: white;

}

@media screen and (min-width:900px)
{
  .flex{
    flex-wrap: nowrap;
  }
  .box{
    margin: 1em;
    max-width: 300px;
    line-height: 1.5;
    font-size: 2em;
  }
  .covidTable{
    flex-wrap: nowrap;
  }
  .covidTable{
    margin: 1em;
    min-width: 200px;
    line-height: 1.5;
  }
}

